import {Container, Row, Col} from "react-bootstrap";
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
export const Contacto = () => {
  useEffect(() => {
    Aos.init({duration: 2000});   
   }, []);

return(
<div data-aos="fade-down" className="formulario" id="contacto">
  <Row className="maldito">
        <Col className="datos">
            <ul className="contact">
                <p>Consecionaria</p> 
                <p><i class="fa-solid fa-location-pin"></i> Direccion falsa 123</p>
                <p><i class="fa-solid fa-phone"></i> 341215215289</p>
                    
            </ul>
        </Col>
        <Col>
      <form action="https://formsubmit.co/athenastorecs@gmail.com"  method="POST">
      
      <input className="inputt" type="text" name="nombre" placeholder="Nombre" required />
      <input className="inputt" type="text" name="apellido" placeholder="Apellido " required />
      <input className="inputt" type="text" name="email" placeholder="Email" required />
      <textarea 
        name="mensaje"
        placeholder="Escriba su mensaje"
        required
      ></textarea>
      <input type="submit" value="ENVIAR" id="botonf" />
    </form></Col>
    </Row></div>
)


}
export default Contacto;