import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';

export const NavBar = () =>{
    
    
    
    return(
   


 
    <Navbar className='nb' expand="lg" sticky="top">
      <Container >
        <Navbar.Brand className='text-light' href="#inicio">Consecionaria</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse   id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link className='text-light'  href="#inicio">Inicio</Nav.Link>
            <Nav.Link className='text-light' href="#nosotros">Nosotros</Nav.Link>
            <Nav.Link className='text-light' href="#contacto">Contacto</Nav.Link>
            <NavDropdown title="Autos" id="basic-nav-dropdown">
              <NavDropdown.Item href="#nuevos">0km</NavDropdown.Item>
              <NavDropdown.Item href="#usados">
                Usados
              </NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#vende">Vende tu auto</NavDropdown.Item>
          
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>


    )


}

export default NavBar;