import React, { useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import banner1 from "../Archivos/Imagenes/banner1.jpg"




export const Banner = () => {

    const [index, setIndex] = useState(0);
    

  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
  };


    return (
        <Carousel id='inicio' activeIndex={index} onSelect={handleSelect} >
      <Carousel.Item>
        <img
          className="bann d-block w-100"
          src={banner1}
          alt="First slide"
          height={600}
        />
        <Carousel.Caption>
        <h3>Autos 0Km</h3>
          <p>Saca tu 0Km financiado </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="bann d-block w-100"
          src={banner1}
          alt="Second slide"
          height={600}
        />

        <Carousel.Caption>
          <h3>Autos de segunda mano</h3>
          <p>Inspeccionamos los autos en varios puntos para corroborar su calidad. </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="bann d-block w-100"
          src={banner1}
          alt="Third slide"
          height={600}
        />

        <Carousel.Caption>
          <h3>Vende tu auto</h3>
          <p>
           Traenos tu auto y te lo cotizamos en el dia!
          </p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
    )
}

export default Banner;
